<template>
  <div class="login">
    <div class="logo">
      <!-- <img src="../assets/logo.jpg" alt="my logo pic"> -->
    </div>
    <!-- 手机号 -->
    <InputGroup
      placeholder="手机号"
      v-model="phone"
      type="number"
      :disabled="disabled"
      :btnTitle="btnTitle"
      :error="errors.phone"
      @btnClick="getVerityCode()"
    />

    <!-- 验证码 -->
    <InputGroup placeholder="验证码" v-model="verifyCode" type="number" :error="errors.code"/>

    <!-- 用户服务协议 -->
    <div class="login_des">
      <p>
        新用户登录即自动注册，表示已同意
        <span>《用户服务协议》</span>
      </p>
    </div>

    <!-- 登录按钮 -->

    <div class="login_btn">
      <button :disabled="!isClick" @click="handleLogin">登录</button>
    </div>
  </div>
</template>
<script>
import InputGroup from "../components/InputGroup";
import { truncate } from "fs";
import { setInterval, clearInterval } from "timers";
export default {
  name: "login",
  components: {
    InputGroup
  },
  data() {
    return {
      phone: "",
      verifyCode: "",
      errors: {
        phone: "",
        code: ""
      },
      btnTitle: "获取验证码",
      disabled: false
    };
  },
  computed: {
    isClick() {
      if (this.phone && this.verifyCode) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    handleLogin() {
      this.$axios.post('/api/posts/sms_back',{
        phone:this.phone,
        code:this.verifyCode
      })
      .then(res=>{
        console.log(res);
        // 检验成功 设置登录状态并且跳转到/
        localStorage.setItem('ele_login',true);
        this.$router.push('/');
      })
      .catch(err =>{
        this.errors= {
          code:  err.response.data.msg
        }
      })
    },
    getVerityCode() {
      if (this.validatePhone()) {
        //发送http请求
        this.validateBtn();
        // 发送网络请求
        this.$axios
          .post("/api/posts/sms_send", {
            tpl_id: "166092",
            key: "453cdb34d18ad240f47400a6eccd6c5c",
            phone: this.phone
          })
          .then(res => {
            console.log(res);
          });
      }
    },
    validateBtn() {
      let time = 15;
      let timer = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          this.btnTitle = "重新获取验证码";
          this.disabled = false;
        } else {
          this.btnTitle = time + "秒后重试";
          time--;
          this.disabled = true;
        }
      }, 1000);
    },
    validatePhone() {
      if (!this.phone) {
        this.errors = {
          phone: "输入的号码不能为空"
        };
        // console.log(this)
        console.log("空");
        return false;
      } else if (!/^1[3,4,5,7,8,9]\d{9}$/.test(this.phone)) {
        this.errors = {
          phone: "手机号格式不正确"
        };
        console.log("手机号格式不正确");
        return false;
      } else {
        this.errors = {};
        console.log("right");
        return true;
      }
    }
  }
};
</script>

<style >
.login {
  width: 100%;
  height: 100%;
  padding: 30px;
  box-sizing: border-box;
  background: #fff;
}

.logo {
  text-align: center;
}
.logo img {
  width: 150px;
}
.text_group,
.login_des,
.login_btn {
  margin-top: 20px;
}
.login_des {
  color: #aaa;
  line-height: 22px;
}
.login_des span {
  color: #4d90fe;
}
.login_btn button {
  width: 100%;
  height: 40px;
  background-color: #48ca38;
  border-radius: 4px;
  color: white;
  font-size: 14px;
  border: none;
  outline: none;
}
.login_btn button[disabled] {
  background-color: #8bda81;
}
</style>
